import { createContext, useContext } from 'react';

// Crer le contexte avec une structure incluant aussi locomotiveScroll
export const ScrollContext = createContext({
  scrollTo: () => {},
  locomotiveScroll: null  // Ajouter cette ligne pour rfrencer l'instance
});

// Crer un hook personnalis pour accder au contexte
export const useScroll = () => useContext(ScrollContext);
