import { Stack, Accordion, AccordionDetails, AccordionSummary, Box, Container, Divider, Typography } from '@mui/material';
import React, { useEffect, useRef } from 'react';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { useScroll } from '../utils/ScrollContext';

const questionsList = [
    {
        id: 0,
        question: 'Qu\'est ce que la conciergerie Cocoon ?',
        answer: 'La Conciergerie Cocoon est une entreprise spcialise dans la gestion locative saisonnire, ddie  transformer votre proprit en une exprience parfaite pour les voyageurs tout en maximisant votre rentabilit. Notre mission est de fournir une gestion de location cl en main qui enlve le fardeau de la gestion quotidienne aux propritaires immobiliers.'
    },
    {
        id: 1,
        question: ' qui s\'adresse les services de la conciergerie Cocoon ?',
        answer: 'Nos services sont destins aux propritaires de biens meubls et bien entretenus qui souhaitent offrir une exprience de qualit  leurs htes. Nous collaborons avec des clients qui partagent notre engagement envers l\'excellence dans l\'accueil et le confort des voyageurs. Si vous possdez un logement en bon tat, dcor avec soin et prt  accueillir des htes, c\'est avec plaisir que nous travaillerons avec vous !'
    },
    {
        id: 2,
        question: 'Comment dterminez-vous le prix de location de mon logement ?',
        answer: 'Le prix est tabli en fonction de plusieurs facteurs, tels que la saisonnalit, l\'emplacement, la taille, les quipements du logement et les tarifs du march local. Nous utilisons des donnes de march actuelles pour optimiser les tarifs et maximiser vos revenus, tout en restant comptitifs.'
    },
    {
        id: 3,
        question: 'Puis-je choisir les locataires ?',
        answer: 'Oui, vous pouvez dfinir des critres de slection des locataires. Nous nous chargeons du processus de vrification selon vos critres (animaux accepts ou non, maximum de voyageurs autoriss... ) et slectionnons les voyageurs selon vos conditions.'
    },
    {
        id: 4,
        question: 'Que se passe-t-il en cas de dommages ou de problmes pendant la location ?',
        answer: 'En cas de dommages ou de problmes pendant la location, nous intervenons rapidement pour rsoudre la situation. Nous travaillons en troite collaboration avec les plateformes de rservations, qui offrent des assurances couvrant les dommages et les vols, afin de garantir que tout soit gr efficacement et sans tracas pour le propritaire.'
    },
    {
        id: 5,
        question: 'Est-ce-que je pourrai encore occuper mon logement si je le loue sur Airbnb ou Booking ?',
        answer: 'Bien sr ! Vous tes matres du calendrier des rservations. Vous pouvez nous communiquer les dates o vous souhaitez occuper votre logement. Le logement tant contrl  chaque sortie de voyageurs, vous avez la garantie de trouver votre logement dans un tat de propret irrprochable.'
    }
]

const Faq = () => {

    const timeoutRef = useRef(null);

    const { locomotiveScroll } = useScroll();

    const updateLocomotiveScroll = () => {
        if (locomotiveScroll) {
            setTimeout(() => {
                locomotiveScroll.update();
              }, 300); 
        }
    }

    useEffect(() => {
        const currentTimeout = timeoutRef.current;
    
        return () => {
            if (currentTimeout) {
                clearTimeout(currentTimeout);
            }
        };
    }, []);

    return (
        <Box
            bgcolor='#fff5e0'
        >
            <Container
                sx={{
                    width: '100vw',
                    padding: '40px 7%',
                }}
            >
                <Stack marginBottom='15px' >
                    <Typography 
                        fontFamily='"Dosis", sans-serif'
                        fontSize='1.6em'
                        fontWeight='500'
                        color='#5a371d'
                    >
                        FAQ <span style={{ fontFamily: '"League Spartan", sans-serif', fontSize: '1.4em', fontWeight: '700', letterSpacing: '-3.5px' }}>Cocoon</span>
                    </Typography>
                </Stack>
                <Stack spacing={3}>
                    {
                        questionsList.map((faq, index) => {
                            return(
                                <React.Fragment key={faq.id}>
                                    <Accordion 
                                        sx={{ 
                                            bgcolor: 'transparent', 
                                            boxShadow: 'none', 
                                            marginBottom: '10px',
                                            '&::before': {
                                                backgroundColor: 'transparent'
                                              }
                                        }}
                                        onClick={() => updateLocomotiveScroll()}
                                    >
                                        <AccordionSummary
                                            expandIcon={<ExpandMoreIcon />}
                                            aria-controls="panel2-content"
                                            id="panel2-header"
                                        >
                                            <Typography
                                                fontFamily='Dosis, sans-serif'
                                                fontWeight='600'
                                                fontSize='1.1em'
                                                paddingRight='10px'
                                                sx={{
                                                    color: '#5a371d'
                                                }}
                                            >
                                                {faq.question}
                                            </Typography>
                                            </AccordionSummary>
                                            <AccordionDetails>
                                                <Typography
                                                    fontFamily='Dosis, sans-serif'
                                                    fontWeight='400'
                                                    fontSize='1.1em'
                                                    sx={{
                                                        color: '#6c462a'
                                                    }}
                                                >
                                                    {faq.answer}
                                                </Typography>
                                        </AccordionDetails>
                                    </Accordion>
                                    {
                                        questionsList.length - 1 === index ?
                                            null
                                        :
                                            <Divider />
                                    }
                                </React.Fragment>
                            )
                        })
                    }
                </Stack>
            </Container>
        </Box>
    );
};

export default Faq;