import { Container, Box, Stack } from '@mui/material';
import React from 'react';
import cameraImg from '../../medias/images/camera.webp';
import laptopImg from '../../medias/images/laptop.webp';
import keysImg from '../../medias/images/keys.webp';
import productsImg from '../../medias/images/products.webp';
import assistanceImg from '../../medias/images/assistance.webp';
import cleaningImg from '../../medias/images/cleaning.webp';
import ServiceCard2 from './ServiceCard2';

const servicesContent = [
    {
        id: 0,
        bgcolor: '#bfa484',
        direction: 'row',
        img: cameraImg,
        title: 'Mise en valeur de votre bien',
        description: <>Nous sublimons votre proprit avec des <strong>photographies professionnelles</strong> et des <strong>annonces rdiges avec soin</strong> pour maximiser son attrait sur les plateformes de rservation.</>
    },
    {
        id: 1,
        bgcolor: '#c4b4a1',
        direction: 'row-reverse',
        img: laptopImg,
        title: 'Gestion des rservations',
        description: <>Nous <strong>grons vos annonces</strong> sur des plateformes telles que Booking et Airbnb, en assurant une <strong>rponse rapide et efficace</strong> aux requtes des voyageurs.</>
    },
    {
        id: 2,
        bgcolor: '#e4d9cd',
        direction: 'row',
        img: keysImg,
        title: 'Entre / Sortie des voyageurs',
        description: <>Nous <strong>accueillons chaleureusement les voyageurs</strong>, grons la remise et la rcupration des cls, et assurons un <strong>tat des lieux mticuleux</strong>  chaque rotation.</>
    },
    {
        id: 3,
        bgcolor: '#bfa484',
        direction: 'row-reverse',
        img: productsImg,
        title: 'Accueil personnalis',
        description: <>Nous mettons  disposition un <strong>livret d'accueil</strong> afin de guider les voyageurs lors de leur sjour. Nous offrons galement une slection de <strong>produits de bienvenue</strong>, comme du vin et des spcialits locales.</>
    },
    {
        id: 4,
        bgcolor: '#c4b4a1',
        direction: 'row',
        img: assistanceImg,
        title: 'Assistance aux voyageurs',
        description: <>Disponibles <strong>24h/24 et 7j/7</strong>, nous offrons une <strong>assistance continue aux voyageurs</strong>, prts  rsoudre tout problme qui pourrait survenir durant leur sjour.</>
    },
    {
        id: 5,
        bgcolor: '#e4d9cd',
        direction: 'row-reverse',
        img: cleaningImg,
        title: 'Mnage et entretien',
        description: <>Nous effectuons un <strong>mnage de niveau htelier</strong>, nous occupons de la blanchisserie et de la maintenance courante, tout en fournissant les consommables (papier toilette, th, caf...)</>
    },
]

const Services = () => {
    return (
        <Box
            bgcolor='#e9d4bc'
            sx={{
                marginTop: {
                    xs: '150px',
                    md: '200px'
                },
            }}
        >
            <Container
                maxWidth={false}
                sx={{
                    width: '100vw',
                    padding: {
                        xs: '40px 5vw 60px 5vw',
                        md: '40px 5vw 80px 5vw'
                    }
                }}
            >
                <Stack 
                    id='services' 
                    marginTop='-162px'
                    sx={{
                        rowGap: {
                            xs: '40px',
                            md: '100px'
                        }
                    }}
                >
                    {
                        servicesContent.map((service) => {
                            return(
                                <ServiceCard2 
                                    key={service.id}
                                    direction={service.direction}
                                    title={service.title}
                                    img={service.img}
                                    description={service.description}
                                />
                            )   
                            
                        })
                    }
                </Stack>
            </Container>
        </Box>
    );
};

export default Services;